import { Injectable } from '@angular/core';
import { Model } from '../api/models/model';
import { ModalController } from '@ionic/angular';
import { AlertCreateTeamPage } from '../pages/payment/alert-create-team/alert-create-team.page';

@Injectable({
  providedIn: 'root'
})
export class PaymentTeamService {

  private team : any;

  public active = false;

  constructor(
    public modalController : ModalController,

  ) { 

  }

  setTeam(team : any){
    this.team  = team;
  }

  isActive(){
    return this.active;
  }


  noPaid(){
    return  this.active ?  ! this.team.paid : false ;
  }

  async showAlert(){
    const modal = await this.modalController.create({
      component: AlertCreateTeamPage ,
      componentProps: { 
           id: this.team.id,
         }
       });
 
       modal.onDidDismiss().then(data=>{
         console.log('return paid')
       })
 
      return await modal.present();
  }
}
